import User from './User'
var md5 = require('md5');

export default class Database
{
    users = null;
    constructor()
    {
        if (localStorage.getItem("")) {
            this.users = JSON.parse(localStorage.getItem(""));
        }
        else {
            this.users = Array();
            this.users.push(new User(1, "admin", "admin@reactmag.ro", "5d35b9f68bb50172ce408c98304f53e1", true));
        }
    }

    queryUser(identificator, pass)
    {
        var mod = md5(pass);
        for (var i=0;i<this.users.length;i++)
        {
            if (this.users[i].username == identificator || this.users[i].email == identificator) {
                if (this.users[i].password == mod) {
                    return this.users[i];
                }
            }
        }
        return null;
    }

    queryIdentificator(identificator)
    {
        for (var i=0;i<this.users.length;i++)
        {
            if (this.users[i].username == identificator || this.users[i].email == identificator) {
                    return this.users[i];
            }
        }
        return null;
    }

    createAccount(name, email, pass, admin=false)
    {
        if (this.queryIdentificator(name) == null && this.queryIdentificator(email) == null)
        {
            pass = md5(pass);
            this.users.push(new User(this.users[this.users.length-1].id+1, name, email, pass, admin));
            localStorage.setItem("", JSON.stringify(this.users));
            return true;
        }
        return false;
    }
}