export default class User
{
    user_id = 0;
    username = "";
    email = "";
    password = "";
    is_admin=false;
    constructor(id, name, mail, pass, admin)
    {
        this.user_id = id;
        this.username = name;
        this.email = mail;
        this.password = pass;
        this.is_admin = admin;
    }
}