import React, {Component} from 'react';
import getAppManager from "../Models/AppManager";

class RegisterPage extends Component {

    constructor()
    {
        super();
        this.state = {
            eroare:"",
            name:"",
            email:"",
            password:""
        };

        this.submitForm = this.submitForm.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        if (event.target.id == "exampleInputName1")
            this.setState({name: event.target.value});
        if (event.target.id == "exampleInputEmail1")
            this.setState({email: event.target.value});
        if (event.target.id == "exampleInputPassword1")
            this.setState({password: event.target.value});
    }

    submitForm(event)
    {
        event.preventDefault();
        var instance = getAppManager();
        var a = instance.createUser(this.state.name, this.state.email, this.state.password);
        if (a !== true) {
            this.setState({eroare: a});
            this.setState({email: ""});
            this.setState({name: ""});
            this.setState({password: ""});
        } else {
            document.location = "/login";
        }
    }

    render() {
        return (
            <div>
                <h1>Inregistrare</h1>
                <form>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Nume</label>
                        <input type="text" value={this.state.name} onChange={this.handleChange} className="form-control" id="exampleInputName1" aria-describedby="numeHelp"
                               placeholder="Introduceti numele"/>
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Adresa email</label>
                        <input type="email" value={this.state.email} onChange={this.handleChange} className="form-control" id="exampleInputEmail1"
                               aria-describedby="emailHelp"
                               placeholder="Introduceti adresa email"/>
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputPassword1">Parola</label>
                        <input type="password" value={this.state.password} onChange={this.handleChange} className="form-control" id="exampleInputPassword1"
                               placeholder="Introduceti parola"/>
                    </div>
                    {this.state.eroare != "" &&
                        <div className="alert alert-danger" role="alert">
                            {this.state.eroare}
                        </div>
                    }
                    <button onClick={this.submitForm} type="submit" className="btn btn-primary">Trimite</button>
                </form>
            </div>
        );
    }
}

export default RegisterPage;
