import User from './User'
import Database from "./Database";

class AppManager {

    static myInstance = null;

    user = null;
    database = null;
    
    static getInstance() {
        if (AppManager.myInstance == null) {
            AppManager.myInstance = new AppManager();
        }

        return this.myInstance;
    }

    constructor()
    {
        this.database = new Database();
        if (localStorage.getItem("")) {
            this.user = JSON.parse(localStorage.getItem(""));
        } else this.user = null;
    }

    getUser() {
        return this.user;
    }

    setUser(u) {
        this.user = u;
    }

    loginUser(identificator, pass)
    {
        var rez = this.database.queryUser(identificator,pass);
        if (rez!=null) {
            var x = AppManager.getInstance();
            x.setUser(rez);
            localStorage.setItem("", JSON.stringify(rez));
            return true;
        }
        return "Credentiale incorecte!";
    }

    createUser(name, email, pass) {
        if (name.length < 3) return "Numele este prea scurt!";
        if (email.length < 3) return "Email-ul este prea scurt!";
        if (pass.length < 3) return "Parola este prea scurta!";
        if (this.database.createAccount(name,email,pass) == true) {
            return true;
        }
        return "Numele sau email-ul exista in baza de date!";
    }
}

function getAppManager()
{
    return AppManager.getInstance();
}

export default getAppManager;