import React from 'react';
import getAppManager from "../Models/AppManager";

function Navbar() {

    var user = getAppManager().getUser();

    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
            <a className="navbar-brand" href="/home">
                <img src="https://png2.cleanpng.com/sh/fe64fa0b48235426816229777df94374/L0KzQYm3V8E5N6J0R91yc4Pzfri0kvVia6UygtNBYYPmgrr3lL13fZYyguU2bHBqf376gBlmdpRqRddDcHX1eb7sjwR0NWZnTNhqNkW4QLK6VfM5Nmk8Uag7N0G1QYa6Uck1PmY9SaI9MUixgLBu/kisspng-react-javascript-vue-js-logo-science-experiments-5b4fa6550a35c8.8796271215319465810418.png" width="35" height="30"
                     className="d-inline-block align-top" alt="" />
                <span style={{"marginLeft":"10px"}}>Reactmag</span>
            </a>

            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup"
                    aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup" style={{"float":"right"}}>
                {user != null &&
                    <div className="navbar-nav">
                        <span>{user.name}</span>
                        <a className="nav-item nav-link" href="/home">Acasa</a>
                        <a className="nav-item nav-link" href="/myaccount">Contul meu</a>
                        {user.is_admin == true &&
                        <a className="nav-item nav-link" href="/sup3r_s3cr3t_4dm1n_p4g3_1337">Setari admin</a>
                        }
                        <a className="nav-item nav-link" href="/logout">Iesire</a>
                    </div>
                }
                {user == null &&
                <div className="navbar-nav">
                    <a className="nav-item nav-link" href="/home">Acasa</a>
                    <a className="nav-item nav-link" href="/login">Autentificare</a>
                    <a className="nav-item nav-link" href="/register">Inregistrare</a>
                </div>
                }
            </div>
        </nav>
    );
}

export default Navbar;
