import React from 'react';
import getAppManager from "../Models/AppManager";

function AccountPage() {

    var user = getAppManager().getUser();

    if (user==null) {
        document.location='/login';
    }

    return (
        <div className="jumbotron">
            <h1 className="display-4">Contul clientului</h1>
            <p className="lead">Salut, {user.username}</p>
            <hr className="my-4" />
            <p>Momentan site-ul este in constructie. Va multimim pentru intelegere!</p>
        </div>
    );
}

export default AccountPage;
