import React, {Component} from 'react';
import getAppManager from "../Models/AppManager";
import * as md5 from "md5";


class LoginPage extends Component {

    constructor()
    {
        super();
        this.state = {
            eroare:"",
            identificator:"",
            password:""
        };

        this.submitForm = this.submitForm.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        if (event.target.id == "exampleInputEmail1")
            this.setState({identificator: event.target.value});
        if (event.target.id == "exampleInputPassword1")
            this.setState({password: event.target.value});
    }

    submitForm(event)
    {
        event.preventDefault();
        var instance = getAppManager();
        var a = instance.loginUser(this.state.identificator, this.state.password);
        if (a !== true) {
            this.setState({eroare: a});
            this.setState({identificator: ""});
            this.setState({password: ""});
        } else {
            document.location = "/myaccount";
        }
    }

    render() {
        return (
            <div>
                <h1>Autentificare</h1>
                <form>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Nume sau Adresa email</label>
                        <input type="text" value={this.state.identificator} onChange={this.handleChange} className="form-control" id="exampleInputEmail1"
                               aria-describedby="emailHelp"
                               placeholder="Nume"/>
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputPassword1">Parola</label>
                        <input type="password" value={this.state.password} onChange={this.handleChange} className="form-control" id="exampleInputPassword1"
                               placeholder="Parola"/>
                    </div>
                    {this.state.eroare != "" &&
                        <div className="alert alert-danger" role="alert">
                            {this.state.eroare}
                        </div>
                    }
                    <button type="submit" onClick={this.submitForm} className="btn btn-primary">Trimite</button>
                </form>
            </div>
        );
    }
}

export default LoginPage;
